h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.link {
  color: $black-color;
  text-decoration: none;
}

.list {
  list-style: none;
}

body {
  font-family: 'Roboto', sans-serif;;
  color: $black-color;
  background-color: $white-color;
}
body.modal-open {
  overflow: hidden;
}